.page {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.btn {
  width: 5.2rem;
  height: 1.2rem;
  background: linear-gradient(0deg, #ff4430, #ff9473);
  border-radius: 1.2rem;
  color: #fff;
  font-size: 0.32rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icon {
  width: 0.6rem;
  height: 0.6rem;
  display: inline-block;
  margin-right: 0.2rem;
}
#fileInput {
  position: fixed;
  left: 9999px;
}

/* loading */
.loading {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.info01 {
  margin-top: 0.2rem;
  font-size: 0.32rem;
  font-weight: bold;
}
.info02 {
  margin-top: 0.2rem;
  color: #756d6d;
  font-size: 0.24rem;
}
.hide {
  display: none;
}
.loop {
  animation: round 1.6s infinite linear;
}
/* 进度 */
.progress {
  width: 1.6rem;
  height: 1.6rem;
  font-size: 0.32rem;
  color: #ff4430;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.num {
  font-size: 0.3rem;
  transform: translate(.09rem, .05rem);
}
.circle {
  width: 1.6rem;
  height: 1.6rem;
  border: 2px solid #ff4430;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}
.dot {
  content: '';
  display: inline-block;
  width: 0.3rem;
  height: 0.3rem;
  background: #ff4430;
  border-radius: 50%;
  position: absolute;
  top: 0.1rem;
  left: 0.1rem;
}

@keyframes round {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
